<template>
    <div>
        <div v-for="(i, index) in allInsts">
            <expression-atom :name="`Inst-${index+1}`" :variables="fields" :instances="providedInsts[index]" @alter="alterInst($event, index)"></expression-atom>
            <expression-note :instance="i" mode="code" prefix="MYSQL: "></expression-note>
        </div>
        <div style="cursor: pointer; font-size: 1.5em; margin-top: 40px" @click="addInst">+</div>
    </div>
</template>

<script>

    import ExpressionAtom from '../../mould-v/clause/ExpressionAtom'
    import ExpressionNote from '../../mould-v/explain/ExpressionNote'
    import { Table, Field, Enum, Operators, i18n } from '../../mould';

    export default {

        name: 'demo-en',

        components: {ExpressionAtom, ExpressionNote},

        data(){
            return {
                fields: [],
                allInsts: [null, null],
                providedInsts: []
            }
        },

        computed: {

        },

        mounted(){

            i18n.switchTo('en');
            Operators.activateContext('mysql-cn');

            let allData = [
                new Table({
                    name: 'USER_INFO',
                    label: 'users\' basic info'
                }).addFields([
                    new Field({
                        name: 'USER_ID',
                        label: 'user id',
                        dataType: 'string'
                    }),
                    new Field({
                        name: 'USER_NAME',
                        label: 'user name',
                        dataType: 'string'
                    }),
                    new Field({
                        name: 'AGE',
                        label: 'age',
                        dataType: 'number',
                        distributeType: 'metrics',
                        unit: 'year'
                    }),
                    new Field({
                        name: 'GENDER',
                        label: 'gender',
                        dataType: 'number',
                        distributeType: 'category',
                    }).addEnums([
                        new Enum({
                            code: 0,
                            label: 'male'
                        }),
                        new Enum({
                            code: 1,
                            label: 'female'
                        })
                    ])
                ]),

                new Table({
                    name: 'COMMODITY_INFO',
                    label: 'commodities\' info'
                }).addFields([
                    new Field({
                        name: 'COMMODITY_ID',
                        label: 'commodity id',
                        dataType: 'string'
                    }),
                    new Field({
                        name: 'COMMODITY_NAME',
                        label: 'commodity name',
                        dataType: 'string'
                    }),
                    new Field({
                        name: 'UNIT_PRICE',
                        label: 'unit price',
                        dataType: 'number',
                        distributeType: 'metrics',
                        unit: 'USD'
                    }),
                ]),

                new Table({
                    name: 'COMMODITY_PURCHASE_LOG',
                    label: 'commodities\' transaction'
                }).addFields([
                    new Field({
                        name: 'COMMODITY_ID',
                        label: 'commodity id (disabled)',
                        dataType: 'string'
                    }),
                    new Field({
                        name: 'USER_ID',
                        label: 'user id (disabled)',
                        dataType: 'string'
                    }),
                    new Field({
                        name: 'QUANTITY',
                        label: 'quantity',
                        dataType: 'number',
                        distributeType: 'metrics',
                        unit: 'unit'
                    }),
                    new Field({
                        name: 'DISCOUNT',
                        label: 'discount',
                        dataType: 'string',
                        distributeType: 'category',
                    }).addEnums([
                        new Enum({
                            code: 'none',
                            label: 'no discount'
                        }),
                        new Enum({
                            code: '0.8-all',
                            label: '20% off for total'
                        }),
                        new Enum({
                            code: '0.5-sec',
                            label: '50% off for the second'
                        }),
                        new Enum({
                            code: 'free',
                            label: 'free'
                        })
                    ])
                ])
            ];


            this.fields = [];
            allData.forEach(t => {
                this.fields.push(...t.getFields());
            })

        },

        methods: {

            alterInst(inst, index){
                this.allInsts[index] = inst;
                this.updateProvided();
            },

            addInst(){
                this.allInsts.push(null);
                this.updateProvided();
            },

            updateProvided(){
                this.$nextTick(() => {
                    let arr = [];
                    let len = this.allInsts.length;
                    this.allInsts.forEach((i, index) => {
                        arr.push(this.allInsts.slice(0, index));
                    });
                    this.providedInsts = arr;
                });
            }
        },

        watch: {


        }

    }
</script>

<style scoped>

